import React, { Component } from 'react';
import { Card, Row, Col, Table, Input, Form, message } from 'antd';
import { BtnSave, BtnCancel, BtnSearch } from 'components/common/button';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getCCSForRestudy, createOrUpdateCCSForRestudy } from 'services/classCreditSchedule';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getResgisterSessionSelect } from 'services/registerSession';
import './style.scss';

@Form.create({ name: 'formRegiterStudyAgain' })
class RegisterStudyAgainPage extends Component {
  constructor (props) {
    super(props);
    this.state = {
      loading: false,
      ccstableData: [],
      classCreditId: null,
      selectedCCSRow: null,
      selectedRowKeys: []
    };

    this.columns = [
      {
        title: 'Lớp học lại',
        dataIndex: 'label',
        key: 'label',
      },
    ];
  }

  getYearEducationsCustom = async params => {
    const res = await getYearEducations(params);
    return this.updateIdToString(res);
  };

  getSemestersCustom = async params => {
    const res = await getSemesters(params);
    return this.updateIdToString(res);
  };

  updateIdToString = res => {
    const newItems = res.data.items.map(value => {
      const newValue = value;
      newValue.id = value.id.toString();
      return newValue;
    });
    res.data.items = newItems;
    return res;
  };

  handleReset = () => {
    this.props.form.resetFields();
    this.setState({
      ccstableData: [],
      classCreditId: null,
      selectedCCSRow: null,
      selectedRowKeys: []
    })
  };

  handleSearch = (e) => {
    const { selectedStudents, searchStudentValues } = this.props;
    if (!selectedStudents || selectedStudents.length === 0 || Object.keys(searchStudentValues).length === 0) {
      return;
    }
    const { subjectId } = selectedStudents[0]
    this.props.form.validateFields((errValidation, formValues) => {
      e.preventDefault();
      if (errValidation) {
        return;
      }
      this.setState({ loading: true });
      getCCSForRestudy({
        ...formValues,
        subjectId,
        trainTypeId: searchStudentValues.trainTypeId,
        facultyId: searchStudentValues.facultyId
      }).then((res) => {
        this.setState({ ccstableData: res.data.ccSs, classCreditId: res.data.classCreditId });
      }, () => {
        message.error('Có lỗi xãy ra!');
      }).catch(() => {
        message.error('Có lỗi xãy ra!');
      }).finally(() => {
        this.setState({ loading: false });
      });
    });
  };

  handleCreate = async () => {
    const { selectedStudents, searchStudentValues } = this.props;
    const { getFieldValue } = this.props.form;
    const { selectedCCSRow, classCreditId } = this.state;

    const yearEduId = getFieldValue('yearEduIdSecond');
    const semesterId = getFieldValue('semesterIdSecond');
    const registerSessionId = getFieldValue('semesterIdSecond');

    if (
        !selectedStudents 
        || 
        selectedStudents.length === 0 
        || 
        Object.keys(searchStudentValues).length === 0 
        || 
        !classCreditId
        || 
        !yearEduId
        || 
        !semesterId
        || 
        !registerSessionId
      ) {
      return;
    }

    const ccsId = selectedCCSRow && selectedCCSRow.id
    const syllableCode = getFieldValue('syllableCode');

    if (!selectedCCSRow && !syllableCode) {
      message.error('Nhập tên lớp hoặc chọn lớp học lại!');
      return;
    }

    try {
      this.setState({ loading: true });

      const resquestData = {
        classCreditId,
        ccsId,
        syllableCode,
        collectTypeId: searchStudentValues.collectTypeId,
        studentIds: selectedStudents.map(m => m.studentId),
        yearEduId,
        semesterId,
        registerSessionId
      };

      await createOrUpdateCCSForRestudy(resquestData);

      message.success('Đăng ký học lại thành công!');

      this.handleShowRegisterStudyAgainForm(false);

      this.handleReset();
    } catch (err) {
      message.error('Có lỗi xãy ra!');
    } finally {
      this.setState({ loading: false })
    }
  }

  handleShowRegisterStudyAgainForm = (visible) => {
    if (typeof this.props.handleShowRegisterStudyAgainForm === 'function') {
      this.props.handleShowRegisterStudyAgainForm(visible);
    }
  }

  handleOnSelectChanged = (record, selected) => {
    if (selected) {
      this.setState({ selectedCCSRow: record, selectedRowKeys: [record.id] })
    }
    else {
      this.setState({ selectedCCSRow: null, selectedRowKeys: [] })
    }
  }

  render () {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { searchStudentValues } = this.props;
    const { loading, ccstableData, selectedRowKeys, selectedCCSRow } = this.state;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };
    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
        initialValue: searchStudentValues.yearEduId,
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
        initialValue: searchStudentValues.semesterId,
      },
    };
    const rowSelection = {
      selectedRowKeys,
      columnTitle: ' ',
      onSelect: this.handleOnSelectChanged,
    };

    const syllableCode = getFieldValue('syllableCode');
    const yearEduId = getFieldValue('yearEduIdSecond');
    const semesterId = getFieldValue('semesterIdSecond');
    const registerSessionId = getFieldValue('registerSessionIdSecond');

    return (
      <Card title="Đăng ký học lại" >
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} xs={24} sm={12} span={11} />
            <Col xs={24} sm={24} md={2}>
              <div className="btn-group">
                <BtnSearch onClick={this.handleSearch} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <p>
                Nếu có lớp học lại bạn hãy chọn lớp đăng ký học lại ở bên dưới. Nếu chưa có hãy nhập
                vào ô Tên lớp đăng ký học lại.
              </p>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col xs={24} sm={24} md={24}>
              <Form.Item label="Tên lớp đăng ký học lại:">
                {getFieldDecorator('syllableCode', {})(
                  <Input placeholder="Nhập tên lớp" />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={24} md={6}>
              <Form.Item {...formItemLayout} label="Năm học" required>
                {getFieldDecorator('yearEduIdSecond', {})(
                  <SelectAjax
                    service={this.getYearEducationsCustom}
                    setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={24} md={6}>
              <Form.Item {...formItemLayout} label="Học kì" required>
                {getFieldDecorator('semesterIdSecond', {})(
                  <SelectAjax service={this.getSemestersCustom} params={{ yearEduId_2: yearEduId }} />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={24} md={6}>
              <Form.Item {...formItemLayout} label="Đợt" required>
                {getFieldDecorator('registerSessionIdSecond', {})(
                  <SelectAjax
                    service={getResgisterSessionSelect}
                    params={{ yearEduId, semesterId, fullFormat: true }}
                    allowGetData={!!yearEduId && !!semesterId}
                    keyValue="value"
                    typeValue="int"
                  />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={24} md={6} style={{ textAlign: 'right' }}>
              <div className="btn-group">
                <BtnSave disabled={(!!selectedCCSRow || (!!syllableCode && !!yearEduId && !!semesterId && !!registerSessionId)) === false} onClick={this.handleCreate} />
                <BtnCancel onClick={() => this.handleShowRegisterStudyAgainForm(false)} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <Table
                bordered
                rowKey="id"
                rowSelection={rowSelection}
                dataSource={ccstableData}
                columns={this.columns}
                loading={loading}
                pagination={false}
              />
            </Col>
          </Row>
        </Form>
      </Card>
    );
  }
}

export default RegisterStudyAgainPage;


